<template>
    <div class="container">
        <h1>Usuarios</h1>

        <b-alert
            :show="dismissCountDown"
            dismissible
            :variant="mensaje.color"
            @dismissed="dismissCountDown=0"
            @dismiss-count-down="countDownChanged"
            >
            {{mensaje.texto}}
        </b-alert>
         <b-card bg-variant="light">
            <form @submit.prevent="updateUser(userEdit)" v-if="editForm">
                <h3>Editar usuario</h3>
                <input type="text" class="form-control my-2" placeholder="Nombre" 
                v-model="userEdit.firstName">
                <input type="text" class="form-control my-2" placeholder="Apellidos"
                v-model="userEdit.lastName">
                <div>
                    <b-form-select v-model="userEdit.active" :options="options" ></b-form-select>               
                </div>
                <b-button class="btn-warning my-2 btn-block" type="submit">Editar</b-button>
                <b-button class="my-2" @click="editForm=false">Cancelar</b-button>
            </form>

            <form @submit.prevent="addNewUser()" v-if="!editForm">
                <h3>Agregar nuevo usuario</h3>
                <input type="text" class="form-control my-2" placeholder="Nombre" 
                v-model="user.firstName">
                <input type="text" class="form-control my-2" placeholder="Apellidos"
                v-model="user.lastName">
                <b-form-select  v-model="user.active" :options="options" ></b-form-select>               
                <b-button class="btn-success my-2 btn-block" type="submit">Agregar</b-button>
            </form>
         </b-card>

        <table class="table my-5">
            <thead>
                <tr>
                    <th scope="col" class="hidden">#</th>
                    <th scope="col">Nombre</th>
                    <th scope="col">Apellidos</th>
                    <th scope="col">Fecha de Creacion</th>
                    <th scope="col">Activo</th>
                    <th scope="col">Accion</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(user, index) in users" :key="index">
                    <th scope="row" class='hidden'>{{user._id}}</th>
                    <td>{{user.firstName}}</td>
                    <td>{{user.lastName}}</td>
                    <td>{{user.creationDate | formatDate}}</td>
                    <td>{{user.active}}</td>
                    <td>
                        <!--b-button-- @click="alerta()">Acción</!--b-button-->
                        
                        <b-button @click="deleteUser(user._id)" class="btn-danger btn-sm mx-2">Eliminar</b-button>
                        <b-button @click="editUser(user._id)" class="btn-warning btn-sm">Editar</b-button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                // Objeto que recoje los datos del formulario
                user: {firstName: '', lastName: '', active: 'true'},

                // Array que pintya el listado de los usuarios              
                users: [],

                userEdit: {},

                //Variable de activacion de formulario
                editForm: false,

                // Datos del las alertas
                dismissSecs: 5,
                dismissCountDown: 0, 
                mensaje: {color: '', texto: ''},
                
                //Valores del combo  active
                options: [
                    { value: 'true', text: 'Activado', selected: true },
                    { value: 'false', text: 'Desactivado' }
                ],
    
            }
        },
        created() {
            // Cuando se crea el componente lista los usuarios
            this.listUsers();
        },
        methods: {
            // Lista los usuarikos
            listUsers(){
                this.axios.get('/user')
                    .then(res =>{                   
                        this.users = res.data;                   
                    })
                    .catch(e => {
                        console.log(e.response);
                    })
            },
            // Añade un nuevo usujariko
            addNewUser() {
                //console.log(this.user);
                this.axios.post('/new-user', this.user)
                    .then(res => {
                        this.users.push(res.data)
                        this.alertSuccess('Usuario Grabado')

                        //Limpiamos variables de usuario
                        this.user.firstName ='';
                        this.user.lastName = '';
                        this.user.active = 'true';
                    })
                    .catch(e => {
                        console.log("addNewUser", e.response);
                        if(e.response.data.error.errors.firstName.message){
                            this.alertError(e.response.data.error.errors.firstName.message)
                        }
                        
                    })

            },
            // Borra un usuario
            deleteUser(id){
                console.log('Eliminar:', id)
                this.axios.delete(`/user/${id}`)
                    .then(res =>{
                        //Buscamos el indice del dato en el arreglo 
                        const index = this.users.findIndex(item => item._id === res.data._id);
                        //Tenemos el indice y eliminamos del arreglo
                        this.users.splice(index, 1);
                        this.alertSuccess('Usuario eliminado')
                    })
                    .catch(e => {
                        console.log(e)
                        this.alertError('Ha ocurrido un error al borrar usuario')
                    })
            },
            editUser(id) {
                console.log(id)
                this.editForm=true;
                this.axios.get(`/user/${id}`)
                    .then(res => {
                        this.userEdit = res.data;
                    })
                    .catch(e => {
                        console.log(e)
                    })

                //this.axios.put(`/user/${id}`)
            },
            updateUser(item) {
                this.axios.put(`/user/${item._id}`, item)
                    .then(res => {
                        const index = this.users.findIndex(n => n._id === res.data._id);
                        this.users[index].firstName = res.data.firstName;
                        this.users[index].lastName = res.data.lastName;
                        this.users[index].active = res.data.active;
                        this.editForm = false;
                        this.alertSuccess('Usuario editado')
                    })
                    .catch(e => {
                        console.log(e)
                        this.alertError('Ha ocurrido un error al actualizar el usuario')
                    })
            },
            countDownChanged(dismissCountDown) {
                this.dismissCountDown = dismissCountDown
            },
            showAlert() {
                this.dismissCountDown = this.dismissSecs
            } ,
            alertSuccess(mensaje){
                this.mensaje.color = 'success'
                this.mensaje.texto = mensaje
                this.showAlert()
            },
            alertError(mensaje){
                this.mensaje.color = 'danger'
                this.mensaje.texto = mensaje
                this.showAlert()
            },
        }
    }

</script>
<style>
.hidden{
  display:none;
  visibility:hidden;
}
</style>
